"use client";
import { Swiper, SwiperSlide } from "swiper/react";
import { IoMdArrowBack, IoMdArrowForward } from "react-icons/io";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { Spin, Empty } from "antd";
import BasicBar from "../common/basicbar";
import PropertyCard from "./property-card";
import { useEffect, useRef, useState } from "react";

const SelectCProperty = () => {
  const [tabActive, setTabActive] = useState("All, Rent, Sell");
  const swiperRef = useRef(null);
 const [filterData, setFilterData] = useState([]);

  return (
    <div className="container mx-auto">
      <div className="mx-auto text-center">
        <BasicBar
          title="Choose The Car You Need"
          description="Choose the perfect car for your lifestyle‚Äîfuel-efficient, stylish, reliable, and comfortable. Explore our wide range of trusted models today."
        />
        <div className="flex flex-row flex-wrap items-center justify-center gap-2  sm:gap-4 lg:mt-10 md:mt-8 mt-6">
          <div
            onClick={() => setTabActive("All")}
            className={`cursor-pointer px-2 py-1 sm:px-4 sm:py-2 rounded-lg border text-sm sm:text-base ${tabActive === "All"
              ? "bg-[#ED5B1C] text-white"
              : "bg-white text-[#0B0A0A] border-gray-300"
              }`}
          >
            All
          </div>
        
     
        </div>
        <div className="lg:mt-10 md:mt-8 mt-6">
          {filterData?.length > 0 ? (
            <>
              <Swiper
                loop={true}
                onSwiper={(swiper) => {
                  swiperRef.current = swiper;
                }}
                breakpoints={{
                  0: {
                    spaceBetween: 10,
                    slidesPerView: 1,
                  },
                  640: {
                    spaceBetween: 16,
                    slidesPerView: 2,
                  },
                  768: {
                    spaceBetween: 24,
                    slidesPerView: 2,
                  },
                  1024: {
                    spaceBetween: 24,
                    slidesPerView: 3,
                  },
                  1440: {
                    spaceBetween: 24,
                    slidesPerView: 4,
                  },
                }}
                className="mySwiper !py-2"
              >
                {filterData?.map((property, index) => (
                  <SwiperSlide key={property._id || index}>
                    <PropertyCard item={property} />
                  </SwiperSlide>
                ))}
              </Swiper>

              {/* Arrow Buttons */}
              <div className="flex items-center justify-center pt-6 sm:pt-10 space-x-4 sm:space-x-[24px]">
                <button
                  onClick={() => swiperRef.current?.slidePrev()}
                  className="group border-[1px] border-[#A2A8AF] w-[40px] h-[40px] sm:w-[56px] sm:h-[56px] text-base sm:text-lg font-semibold text-white bg-white rounded-[4px] hover:border-white hover:bg-primary transition duration-500 flex items-center justify-center"
                >
                  <IoMdArrowBack className="text-[#A2A8AF] group-hover:text-white transition duration-500" />
                </button>
                <button
                  onClick={() => swiperRef.current?.slideNext()}
                  className="group border-[1px] border-[#A2A8AF] w-[40px] h-[40px] sm:w-[56px] sm:h-[56px] text-base sm:text-lg font-semibold text-white bg-white rounded-[4px] hover:border-white hover:bg-primary transition duration-500 flex items-center justify-center"
                >
                  <IoMdArrowForward className="text-[#A2A8AF] group-hover:text-white transition duration-500" />
                </button>
              </div>
            </>
          ) : (
            <Empty description="No cars found" />
          )}
        </div>
      </div>
    </div>
  );
};

export default SelectCProperty;
